

























































import {Vue, Component, Watch} from "vue-property-decorator";

@Component
export default class NavSider extends Vue {
    routeName:any = '';

    @Watch("$route")
    private handleRouteChange(): void {
        this.routeName = this.$route.name;
    }

    mounted(){
        this.routeName = this.$route.name;
    }
    get openNames(){
        let pname:string = this.$route.meta.pname;
        if(pname){
            return [pname]
        }
        else{
            return []
        }

    }
}
